.MatchPage {

    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 20px;

    .page-heading {
        margin: 20px auto;
    }

    .year-selector-card {
            justify-self: end;
            padding: 20px;
    }

    .home-page-link {
        float : right;
    }

}